@import "@/scss/main.scss";



































































.top-menu {
  flex-basis: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (max-width: $smDesktopWidth) {
    flex-basis: 50%;
  }
  @media screen and (max-width: $phoneWidth) {
    justify-content: flex-end;
  }

  #menu-toggle {
    width: 40px;
    display: none;
    @media screen and (max-width: $phoneWidth) {
      display: block;
    }

    div:first-child {
      margin-top: 0;
    }

    div {
      width: 100%;
      height: 4px;
      background-color: $hover-color;
      margin-top: 5px;
    }
  }

  .menu-toggle_fix {
    font-size: 20px;
    line-height: 1.5;
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    background: #cdf8f9;
    width: 0;
    height: 100%;
    min-height: 500px;
    z-index: 5;
    color: rgba($success-color, 0.6);
    transition: .3s linear;
    &.active {
      width: 100%;
      overflow: visible;
    }

    .menu-toggle-items {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 40px 20px;
      width: 100%;
      height: 100%;
      background: url("../assets/bg-toggle.png") bottom right no-repeat;
      background-size: 320px;
    }

    ul {
      text-align: center;
      margin: 30px 0;

      li {
        color: inherit;
        margin-top: 10px;

        &:first-child {
          margin-top: 0;
        }

        a {
          color: inherit;
        }
      }
    }


    //close menu
    .close {
      width: 20px;
      height: 20px;
      position: absolute;
      right: 5%;
      top: 5%;
    }

    .close:before, .close:after {
      content: "";
      width: 3px;
      display: block;
      height: 100%;
      background-color: $hover-color;
      position: absolute;
      top: 0;
      left: 8px;
    }

    .close:before {
      transform: rotate(45deg);
    }

    .close:after {
      transform: rotate(-45deg);
    }
  }
}

.top-menu-items {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  @media screen and (max-width: $phoneWidth) {
    display: none;
  }

  li {
    a {
      font-size: rem(18px);
      color: rgba($success-color, 0.5);
      letter-spacing: rem(0, 5);
      line-height: rem(36px);
      transition: .2s ease;
    }
  }

  a:hover {
    color: $hover-color;
    font-weight: $fontWeightBlack;
    line-height: rem(36px);
    transform-origin: center;
  }
}

